import React from 'react';


const Portfolio = ( { projects } ) => {
    
    const projectList = projects.length ? (
        projects.map( project => {
            return(
                <div className="col s12 m6 l4" key={ project.id }>
                    <div className="card">
                        <div className="card-image">
                            <img src={ project.img } alt="image" />
                            <span className="card-title">{ project.name }</span>
                        </div>
        
                        <div className="card-content">
                            <p>{ project.desc }</p>
                        </div>
        
                        <div className="card-action">
                            <a href={ project.github }>Github</a>
                            <a href={ project.server }>Server</a>
                        </div>
                    </div>
                </div> 
            )
        } )
    ) : (
    <p>No Project To Show</p>
    );
        
    return(
        <section className="portfolio" id="portfolio">
            <h1 className="section-heading">My Portfolio</h1>
            <div className="container">
                <div className="row">  { projectList }  </div>
            </div>
        </section>
    )
}

export default Portfolio;